import TopAppBar from "../components/TopAppBar"
import { Stack, Box, LinearProgress, Button, Dialog, DialogTitle, Slide, Divider, Card, Chip } from "@mui/material"
import { ChevronLeft, ChevronRight, SentimentDissatisfied } from "@mui/icons-material"
// import { SearchBar } from "../components/SearchBar"
import { CouponCard } from "../components/CouponCard"
import { useEffect, useState } from "react"
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios"
import Confetti from "react-confetti";
import { useRef } from "react";
import { useDraggable } from "react-use-draggable-scroll";
import SeletedFiltersList from "../components/SelectedFiltersList"


export function Home() {
    const navigate = useNavigate();
    const location = useLocation();
    const ref = useRef(); // We will use React useRef hook to reference the wrapping div:
    const { events } = useDraggable(ref); // Now we pass the reference to the useDraggable hook:

    const config = {
        headers: {
            Authorization: localStorage.getItem('token'),
        }
    }
    const [userName, SetUserName] = useState('');
    const [totalCoupons, SetTotalCoupons] = useState('');
    const [noCoupons, SetNoCoupons] = useState(false);
    const [totalFeedbacks, SetTotalFeedbacks] = useState('');
    const [coupons, SetCoupons] = useState([]);
    const [industries, SetIndustries] = useState([]);
    const [selectedIndustries, SetSelectedIndustries] = useState([]);
    const [couponWon, SetCouponsWon] = useState(0);
    const [msgOpen, SetMsgOpen] = useState(false);


    const handleMsgOpen = () => {
        SetMsgOpen(true);
    };

    const handleMsgClose = () => {
        SetMsgOpen(false);
    };

    const handleClearFilter = () => {
        SetSelectedIndustries([]);
    };


    const couponStats = [
        {
            "img": "./assets/cwon.png", "text": "Coupons won", "num": totalCoupons.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
        },
        {
            "img": "./assets/fbgiven.png", "text": "Feedbacks Given", "num": totalFeedbacks.toLocaleString('en-US', {
                minimumIntegerDigits: 2,
                useGrouping: false
            })
        },

    ]
    //  ----------------------------------------------- API Calls -------------------------------------
    useEffect(() => {

        if (location.state) {
            SetMsgOpen(true);
            SetCouponsWon(location.state.couponNum);
        }

        if (localStorage.getItem('token')) {

            // ----------- Get User coupons 

            axios.post('https://extraa.in/go-api/public/api/user-coupon-details', '', config).then(response => {
                // console.log(response.data.RESULT);
                if(response.data.RESULT){
                    SetTotalCoupons(response.data.Data.length)
                    SetCoupons(response.data.Data.reverse());
                } else {
                    SetNoCoupons(true);
                    SetTotalCoupons(0);
                }
              
           
            })

            // ----------- Get User feedback details 

            axios.post('https://extraa.in/go-api/public/api/user-feedback-details', '', config).then(response => {
                // console.log(response.data)
                if(response.data.RESULT)   {
                    SetTotalFeedbacks(response.data.Data.length)
                } else {
                    SetTotalFeedbacks(0);
                }
           
                // SetCoupons(response.data.Data);
            })

            // ----------- Get User profile 

            axios.post('https://extraa.in/go-api/public/api/get-user-profile', '', config).then(response => {
                // console.log(response.data.Data)
            })

            // ----------- Get all industries 

            axios.get('https://extraa.in/go-api/public/api/get-all-industries').then(response => {
                // console.log(response.data['Industry List'])
                SetIndustries(response.data['Industry List']);
            })

        } else {
            navigate('//');
        }
        SetUserName(localStorage.getItem('user_name'));
    }, []);

    //  ------------------------------------- Fitler by industry ----------------------------------------

    const getUserCouponsbyIndustry = (ind_id, event) => {
        let sind = selectedIndustries;
        if (event.target.checked) {
            sind.push(ind_id);
        } else {
            let index = sind.indexOf(ind_id);
            if (index > -1) {
                sind.splice(index, 1);
            }
        }

        SetSelectedIndustries(sind);

        // console.log(selectedIndustries);

        let indData = { "industry_id": sind.join(", ") };

        let indconfig = {
            method: 'post',
            url: 'https://extraa.in/go-api/public/api/user-coupon-details',
            headers: {
                'Authorization': localStorage.getItem('token'),
                'Content-Type': 'application/json'
            },
            data: indData
        };
        // let couponfd = new FormData();
        // couponfd.append('industry_id', ind_id);

        axios(indconfig)
            .then(response => {
                // console.log(response.data)

                if (response.data.Data) {
                    SetCoupons(response.data.Data.reverse());
                    SetNoCoupons(false);
                } else {
                    SetNoCoupons(true);
                }

            })
    }

    return (
        <div className="home">
            <TopAppBar home={true} />
            {/* ------------------------------------- Background Images -------------------------- */}
            <div style={{position:'relative'}} >
                <img className="bgImgs floating" id="bcircle-1" src="./assets/Asset 7.png" alt="" />
                <img className="bgImgs floating" id="halo-1" src="./assets/Asset 5.png" alt="" />
                <img className="bgImgs floating" id="halo-2" src="./assets/Asset 5.png" alt="" />

                <img className="bgImgs floating" id="bcircle-2" src="./assets/Asset 7.png" alt="" />
                <img className="bgImgs floating" id="halo-3" src="./assets/Asset 5.png" alt="" />
                <img className="bgImgs floating" id="halo-4" src="./assets/Asset 5.png" alt="" />

                <img className="bgImgs floating" id="bcircle-3" src="./assets/Asset 7.png" alt="" />
                <img className="bgImgs floating" id="halo-5" src="./assets/Asset 5.png" alt="" />
            </div>
            {/* ------------------------------------------- Dialog ------------------------------------------ */}

            {/* <Confetti

                opacity={msgOpen ? 1 : 0}
                run={msgOpen}
            /> */}
            <Dialog
                open={false}
                keepMounted
                onClose={handleMsgClose}
                aria-describedby="alert-dialog-slide-description"
                fullWidth
            >

                <DialogTitle sx={{ fontFamily: 'rota-bold', textAlign: 'center', background: '#F9B61F' }}> Congratulations!</DialogTitle>

                <Stack alignItems='center'>

                    <img src="./assets/gift.png" alt="" style={{ marginTop: 32 }} />
                    <img src="./assets/extraa_logo.png" alt="" style={{ height: 32, marginTop: 8 }} />

                    {couponWon > 1 ?

                        (<p>
                            You've won <span style={{ fontFamily: 'rota-black' }}> {couponWon}  </span> coupons!
                        </p>) :
                        (
                            <p>
                                You've won <span style={{ fontFamily: 'rota-black' }}>  {couponWon}  </span> coupon!
                            </p>
                        )}


                </Stack>

            </Dialog>




            {/* -------------------------- Welcome Section -------------------------- */}

            <Stack justifyContent={'center'} alignItems='center' pt={6}>
                <h3 style={{ color: '#4F3084', fontSize: '1.6em', margin: 0, paddingLeft: '1em', paddingRight: '1em', textAlign: 'center' }}>
                    Welcome! {userName}
                </h3>
                <Divider sx={{ background: 'red' }} />
                {/* -------------------------- Stats -------------------------- */}

                <Stack justifyContent={'center'} alignItems='center' pt={4} direction='row' gap={4} pb={4}>

                    {couponStats.map(item => (
                        <Card style={{ borderRadius: 24, }} key={item.text} >
                            <Stack p={4} alignItems='center' width={100} justifyContent='space-between' sx={{ textAlign: 'center' }} >
                                <img src={item.img} alt="" />
                                <h3 style={{}}> {item.text}</h3>
                                <h2 style={{ margin: 0, color: '#4F3084', lineHeight: 0, fontFamily: 'rota-black' }}>{item.num}</h2>
                            </Stack>
                        </Card>
                    ))}


                </Stack>
            </Stack>

            {/* -------------------------- Complete your profile -------------------------- */}

            <Stack style={{ background: '#4F3084', color: 'white' }} p={4} alignItems='center'>
                <p> Complete your profile to get better coupons</p>
                <p style={{ textAlign: 'right', width: '100%', maxWidth: '800px', margin: 0, lineHeight: 1 }}> 1/3</p>

                <Box sx={{ width: '100%', maxWidth: '800px' }} mb={4}>
                    <LinearProgress variant="determinate" value={20} color='secondary' />
                </Box>

                <Button variant="contained" className="y-btn" onClick={() => navigate('/user-profile')} >Go to Profile</Button>
            </Stack>

            {/* -------------------------- Search-------------------------- */}

            {/* <Stack alignItems='center' mt={2}>
                <SearchBar></SearchBar>
            </Stack> */}



            {/* -------------------------- Filter by industries -------------------------- */}
            <h3 style={{ marginBottom: 0, color: '#4F3084', textAlign: 'center', fontSize: '1.4em' }}>Your Coupons</h3>

            <Stack alignItems='center'>
                <div style={{ maxWidth: 1000, paddingLeft: 24, width: '100vw' }}>
                    <h4 style={{ marginBottom: 0, color: '#4F3084', textAlign: 'left', fontSize: '1em' }}>Filter by industries:</h4>
                </div>
            </Stack>

            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
                <div style={{ width: '100%', maxWidth: 1000, position: 'relative' }}>
                    <div style={{ position: 'absolute', right: -24, top: 24, zIndex: 100 }}>
                        <ChevronRight sx={{ width: 40, height: 40, opacity: 0.7 }} color='primary' />
                    </div>
                </div>

                <div style={{ display: 'flex', width: '100%', maxWidth: 1000, overflowX: 'scroll', paddingTop: 8, paddingBottom: 8 }}
                    className='filter-scroll'
                    {...events}
                    ref={ref}
                >


                    {industries.map(ind => (
                        <div key={ind.id}>
                            <input type="checkbox" id={'inudstry' + ind.id} onClick={(event) => getUserCouponsbyIndustry(ind.id, event)} />
                            <label htmlFor={'inudstry' + ind.id} >
                                <img style={{ width: 48, height: 48, marginRight: 12, marginLeft: 12 }} src={'https://extraa.in/admin/web/uploads/industry/' + ind.industry_logo} alt="" />

                            </label>
                            <p style={{ textAlign: 'center', margin: 0, fontSize: '0.6em', textTransform: 'capitalize', paddingRight: 8, paddingLeft: 8 }}>{ind.industry_name.toLowerCase().replace('and', '&')}</p>
                        </div>

                    ))
                    }


                </div>

            </div>
            <Stack alignItems='center'>
                <div style={{ maxWidth: 1000, width: '100vw' }}>

                    {/* <SeletedFiltersList
                        selectedIndustries={selectedIndustries}
                        industries={industries}
                    /> */}
                </div>


            </Stack>
            {/* --------------------------  Coupons  -------------------------- */}

            <Stack direction='row' justifyContent='center' >

                {noCoupons ? (

                    <Stack mt={4} mb={8} alignItems='center'>
                        <SentimentDissatisfied sx={{ width: 100, height: 100, opacity: 0.8 }} color='primary' />
                        <p style={{ textAlign: 'center' }}> Sorry, we couldn't find any coupons. <br />Try clearing your filters</p>
                    </Stack>

                ) :
                    (
                        <Slide in direction="up" >
                            <Stack mt={4} mb={8} direction={{ 'sm': 'column', 'lg': 'row' }} gap={4} justifyContent='center' flexWrap='wrap' sx={{ maxWidth: 1200 }}>

                                {coupons.map((obj, index) => (
                                    <CouponCard
                                        key={obj.user_form_id}
                                        brandName={obj.brand.brand_name.toLowerCase()}
                                        offerType={obj.coupon.offer_details.slice(0, 8)}
                                        offerDetails={obj.coupon.offer_details.slice(8).toLowerCase()}
                                        code={obj.coupon.coupon_code}
                                        validity={obj.coupon.expiry_date}
                                        brandLogo={'https://extraa.in/admin/web/uploads/retailer/' + obj.brand.brand_logo}
                                        industry_name={obj.coupon.industry_name.toLowerCase()}
                                        industry_icon={'https://extraa.in/admin/web/uploads/industry/' + obj.coupon.industry_logo}
                                        terms={obj.coupon.terms_and_conditions}
                                        coupon_color={obj.coupon.color_code}
                                        dialogOpen={msgOpen === true && index === 0 ? true : false}
                                    />
                                ))}


                            </Stack>
                        </Slide>
                    )}
            </Stack>

        </div >
    )
}